<!-- 客流监控 -->
<template>
  <van-row class="dt-home">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">客流监测</p>
      </van-col>
    </van-row>
    <van-row>
      <van-field readonly
                 clickable
                 label="区域名称"
                 :value="areaName"
                 placeholder="选择区域"
                 @click="showPicker = true" />
      <van-popup v-model="showPicker"
                 round
                 position="bottom">
        <van-picker show-toolbar
                    :columns="areaList"
                    @cancel="showPicker = false"
                    @confirm="onConfirm" />
      </van-popup>
    </van-row>
    <van-row>
      <van-col span="8"
               class="dt-status-row">
        <div class="dt-box dt-status-box1">
          <div class="dt-text"><span>今日检票数</span></div>
          <div class="dt-number"><span>{{intoNum}}</span></div>
        </div>
      </van-col>
      <van-col span="8"
               class="dt-status-row">
        <div class="dt-box dt-status-box2">
          <div class="dt-text"><span>今日售票数</span></div>
          <div class="dt-number"><span>{{ticketNum}}</span></div>
        </div>
      </van-col>
      <van-col span="8"
               class="dt-status-row">
        <div class="dt-box dt-status-box3">
          <div class="dt-text"><span>昨日检票数</span></div>
          <div class="dt-number"><span>{{yesterdayInto}}</span></div>
        </div>
      </van-col>
    </van-row>
    <van-row class="dt-heatMap-box">
      <heatMap class="dt-heatMap"
               :mapCenter="mapCenter"
               :latLng="latLng"></heatMap>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
// 在园人数，今日入园人数
import {
  // 闸机出入人数
  gateNum
} from "@/api/chart";
import {
  ticketNumber
} from "@/api/chart";
import heatMap from "@/components/map/heatMap";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    heatMap,
  },
  data () {
    //这里存放数据
    return {
      // 今日检票数
      intoNum: 0,
      // 今日售票数
      ticketNum: 0,
      // 昨日检票数
      yesterdayInto: 0,
      showPicker: false,
      areaName: '园区全景',
      latLng: {},
      areaList: ['园区全景', '扬州世园会A片区', '扬州世园会B片区', '扬州世园会C片区', '扬州世园会D片区'],
      areasObj: {
        '园区全景': { lat: 32.33142324513, lng: 119.0921550222, zoom: 14 },
        '扬州世园会A片区': { lat: 32.32707339304338, lng: 119.09970923656995, zoom: 16.5 },
        '扬州世园会B片区': { lat: 32.33137638952233, lng: 119.09665380799598, zoom: 16.5 },
        '扬州世园会C片区': { lat: 32.32890777663665, lng: 119.08253169511838, zoom: 16.5 },
        '扬州世园会D片区': { lat: 32.33640379731052, lng: 119.0822374221716, zoom: 16.5 },
      },
      mapCenter: { lat: 32.33142324513, lng: 119.0921550222 }
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getGateNum()
    // 大屏售票数
    this.getTicketNumber()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 获取闸机人流
    getGateNum () {
      gateNum().then(res => {
        let data = res.data
        let intoNum = data[data.length - 1].inPeople
        let yesterdayInto = data[data.length - 2].inPeople
        this.intoNum = intoNum
        this.yesterdayInto = yesterdayInto
      })
    },
    // 大屏售票数
    getTicketNumber () {
      ticketNumber().then(res => {
        let data = res.data
        this.ticketNum = data[data.length - 1].number
      })
    },
    // 跳转首页
    Home () {
      this.$router.push({ name: 'Home' })
    },
    // 选择区域
    onConfirm (value) {
      this.areaName = value;
      this.latLng = this.areasObj[value]
      this.showPicker = false;
    },
    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
