      color: #ffffff;
      margin: px2rem(12) 0;
    }
  }
  .dt-number {
    span {
      display: block;
      text-align: center;
      font-size: px2rem(16);
      font-family: Source Han Sans CN;
      font-weight: Medium;
      color: #ffffff;
      margin: px2rem(12) 0;
    }
  }
}

