<!-- 热力图 -->
<template>
  <div id="heatMap"></div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import { queryheatmap } from "@/api/chart";
import { getHetMapLonLat } from "@/utils/map";
var map = null
let heat = null
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  props: ['mapCenter', 'latLng'],
  data () {
    //这里存放数据
    return {
      childMapCenter: {}
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
    mapCenter (newValue, oldValue) {
      this.childMapCenter = newValue
    },
    latLng (newValue, oldValue) {
      this.setMapCenter(newValue)
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.childMapCenter = this.mapCenter
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    this.initMap()
    this.getheatmap()

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前

  updated () {
  },
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    initMap () {
      var center = new TMap.LatLng(this.childMapCenter.lat, this.childMapCenter.lng);

      //初始化地图
      map = new TMap.Map("heatMap", {
        pitch: 43.5,  //设置俯仰角
        zoom: 14,//设置地图缩放级别
        center: center,//设置地图中心点坐标
        mapStyleId: "style1", //个性化样式
      });
      //初始化热力图并添加至map图层
      heat = new TMap.visualization.Heat({
        max: 350, // 热力最强阈值
        min: 0, // 热力最弱阈值
        height: 0, // 峰值高度
        gradientColor: { // 渐变颜色
          0.01: "rgb(0, 4, 255)",
          0.2: "rgb(255, 217, 0)",
          0.5: "rgb(255, 0, 0)",
        },
        radius: 30 // 最大辐射半径

      }).addTo(map);


    },
    setMapCenter (areaCenter) {
      var center = new TMap.LatLng(areaCenter.lat, areaCenter.lng);
      map.setCenter(center)
      map.setZoom(areaCenter.zoom)
    },
    getheatmap () {
      queryheatmap().then(res => {
        let hatData = res.data
        this.hatData = getHetMapLonLat(hatData)
        heat.setData(this.hatData);//设置数据
      })
    }
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';
#heatMap {
  width: 100%;
  height: 100%;
  color: rgb(255, 0, 0);
}
</style>